.timepass {
    padding: 20px;
    border-radius: 10px;
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 44.7vh;
}

.error-code {
    font-size: 100px;
    color: #e74c3c;
    margin: 0;
}

.error-message {
    font-size: 24px;
    margin-top: 10px;
}

.back-link {
    margin-top: 20px;
    text-decoration: none;
    color: #3498db;
    font-weight: bold;
    transition: color 0.3s;
}

.back-link:hover {
    color: #1e87d5;
}

.h-100 {
    height: 100%;
}